/* ------------------ FORM STYLE ----------------- */

h2 {
    font-size: 1.5rem;
    text-align: left;
    width: 40rem;
    margin: 1.5rem;
    margin-left: 5rem;
    /* border: 1px solid darkmagenta; */
    padding-bottom: .4rem;
    border-bottom: 2px solid #777;
    
}
form {
    font-size: 1.2rem;
    /* border: 1px solid salmon; */
    position: relative;
    display: flex;
    width: 45rem;
    flex-direction: column;
    align-items: center;
}

form p {
    width: 80%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 10px 0;
    /* border: 1px solid greenyellow; */
    }

form input {
    margin-left: .5rem;
    padding: 5px 10px;
    border-radius: 3px;
    font-family: inherit;
    font-size: 1rem;
    width: 70%;
    border: 0;
    border-bottom: 1px solid #eee;
    box-shadow:0 0 6px 2px rgba(0,0,0,0.06);
}

form .two-col input {
    width: 8.7rem;
}

form .two-col label:last-of-type {
    margin-left: .5rem;
}